.img-container {
  background: #fff;
  border-radius: 2px;
  height: 230px;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  background-size: cover;
  background-position: 50%;
}

.img-container > img {
  width: 100%;
  height: 100%;
}

.img-container > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.img-container > .content > ul {
  list-style-type: none;
}

.img-container > .content > ul > li {
  margin-top: 18px;
}

.img-container > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.img-container > .remove:hover {
  color: #d50000;
}

.friend-card-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row; /* Display images in a row */
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; /* Allow images to wrap to the next row */
  -ms-flex-pack: justify;
      justify-content: space-between; /* Space between images */
}

@media (max-width: 550px) {
  .friend-card-container{
    -ms-flex-pack: center;
        justify-content: center;
  }
} 


